import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {ProductMangerComponent} from './product-manger/product-manger.component';
import {AddProductComponent} from './add-product/add-product.component';
import {EditProductComponent} from './edit-product/edit-product.component';
import {ProductListComponent} from './product-list/product-list.component';
import {ProductListEditComponent} from './product-list-edit/product-list-edit.component';

const routes: Routes = [
  {path: '', component:ProductMangerComponent,data:{breadcrumb: ''}},
  {path: 'edit', component:EditProductComponent,data:{breadcrumb: ''}},
  {path: 'list', component:ProductListComponent,data:{breadcrumb: ''}},
  {path: 'add-product', component:AddProductComponent,data:{breadcrumb: ''}},
  {path: 'edit-product', component:ProductListEditComponent,data:{breadcrumb: ''}}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProductRoutingModule { }
